interface UserRowColorType {
  [key: string]: ThemeColor;
}
const userStatusObj: UserRowColorType = {
  online: "success",
  offline: "warning",
  inactive: "secondary",
};

interface CellType {
  row: any;
}
// Prevent Click Event Bubbling
const disableClickEventBubbling = (params: any) => {
  params?.event?.stopPropagation();
};

const ViewContentLibFolder = () => {
  const [value, setValue] = useState<string>("");
  const [pageSize, setPageSize] = useState<number>(10);
  const [filterList, setFilterList] = useState<any[]>(dummyUsers);
  const [selectedRole, setSelectedRole] = useState("");
  const [page, setPage] = useState<number>(0);
  const [isDrawerOpen, setIsDrawerOpen] = useState(false);
  const [open, setOpen] = useState<boolean>(false);
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down("md"));
  const [selectedId, setSelectedId] = useState<string>("");
  const [selectedUser, setSelectedUser] = useState<any>({});
  const [isDeleting, setIsDeleting] = useState(false);

  const handleClickOpen = (row: any) => {
    setOpen(true);
    setSelectedId(row);
  };

  const handleClose = () => {
    setOpen(false);
    setSelectedId("");
  };

  const handleDelete = () => {
    setIsDeleting(true);
    setTimeout(() => {
      setFilterList(filterList.filter((user) => user.id !== selectedId));
      setIsDeleting(false);
      setOpen(false);
    }, 1000); // Simulate async delete
  };

  const handleFilter = useCallback((val: string) => {
    setValue(val);
    const filteredData = dummyUsers.filter((item: any) => {
      const startsWith =
        item.libraryID.toLowerCase().startsWith(val.toLowerCase()) ||
        item.name.toLowerCase().startsWith(val.toLowerCase());
      const includes =
        item.libraryID.toLowerCase().includes(val.toLowerCase()) ||
        item.name.toLowerCase().includes(val.toLowerCase());
      return val === "" ? item : startsWith || includes;
    });
    setFilterList(filteredData);
  }, []);

  const [sortModel, setSortModel] = useState<any>([
    { field: "libraryID", sort: "asc" },
  ]);

  const handleSortModelChange = (model: any) => {
    setSortModel(model);
  };

  const columns = [
    {
      flex: 0.1,
      minWidth: 200,
      field: "libraryID",
      headerName: "Library ID",
      renderCell: ({ row }: CellType) => {
        const { libraryID } = row;

        return (
          <Box sx={{ display: "flex", alignItems: "center" }}>
            <Box
              sx={{
                display: "flex",
                alignItems: "flex-start",
                flexDirection: "column",
              }}
            >
              <Typography
                noWrap
                component="a"
                sx={{
                  fontWeight: 400,
                  color: theme.palette.text.secondary,
                  textDecoration: "none",
                }}
              >
                {libraryID}
              </Typography>
            </Box>
          </Box>
        );
      },
    },
    {
      flex: 0.2,
      minWidth: 200,
      field: "name",
      headerName: "Name",
      renderCell: ({ row }: CellType) => {
        return (
          <Typography noWrap sx={{ color: "text.secondary" }}>
            {row.name}
          </Typography>
        );
      },
    },
    {
      flex: 0.5,
      minWidth: 350,
      field: "description",
      headerName: "Description",
      renderCell: ({ row }: CellType) => {
        return (
          <Typography noWrap sx={{ color: "text.secondary" }}>
            {row.description}
          </Typography>
        );
      },
    },
    {
      flex: 0.1,
      minWidth: 110,
      field: "status",
      headerName: "Status",
      renderCell: ({ row }: CellType) => {
        return (
          <CustomChip
            rounded
            skin="light"
            size="small"
            label={row.status}
            color={userStatusObj[row.status]}
          />
        );
      },
    },
    {
      flex: 0.1,
      minWidth: 100,
      sortable: false,
      field: "actions",
      headerName: "Actions",
      renderCell: ({ row }: CellType) => (
        <Box sx={{ display: "flex", justifyContent: "center" }}>
          <Tooltip title="Edit User" placement="top" arrow>
            <IconButton
              onClick={() => {
                setIsDrawerOpen(true);
                setSelectedUser(row);
              }}
              color="primary"
            >
              <Icon icon="bx:edit-alt" />
            </IconButton>
          </Tooltip>
          <Tooltip title="Delete User" placement="top" arrow>
            <IconButton
              onClick={() => handleClickOpen(row?.id || row?._id)}
              color="error"
            >
              <Icon icon="bx:trash" />
            </IconButton>
          </Tooltip>
        </Box>
      ),
    },
  ];

  return (
    <Grid container spacing={6}>
      <Grid item xs={12}>
        <Card sx={{ width: "90vw" }}>
          <TableHeader
            handleFilter={handleFilter}
            // toggle={toggleAddUserDrawer}
            placeholder={"Search Library folder..."}
          />
          <Divider sx={{ m: "0 !important" }} />
          <DataGrid
            autoHeight
            sortModel={sortModel}
            onSortModelChange={handleSortModelChange}
            getRowId={(row) => row?.id || row?._id}
            rows={filterList}
            columns={columns}
            loading={usersListLoading}
            rowCount={filterList.length}
            disableSelectionOnClick
            rowsPerPageOptions={[10, 25]}
            checkboxSelection
            paginationMode="server"
            pageSize={pageSize}
            pagination
            page={page}
            onPageChange={(newPage: any) => setPage(newPage)}
            onPageSizeChange={(newPageSize: number) => {
              setPageSize(newPageSize);
              setPage(0);
            }}
            getRowHeight={() => "auto"}
            disableColumnMenu
            disableColumnSelector
            onCellClick={disableClickEventBubbling}
          />
        </Card>
      </Grid>
      {/* {isDrawerOpen && (
        <DrawerEditUser
          open={isDrawerOpen}
          toggle={() => setIsDrawerOpen((pre) => !pre)}
          selectedUser={selectedUser}
          roles={dummyRoles}
        />
      )} */}
      <Dialog
        fullScreen={fullScreen}
        open={open}
        onClose={handleClose}
        aria-labelledby="responsive-dialog-title"
      >
        <DialogTitle id="responsive-dialog-title">Deleting User?</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Are you sure to delete this user? Once you delete, you cannot revert
            this.
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={handleClose}>
            Cancel
          </Button>
          <LoadingButton
            loading={isDeleting}
            onClick={handleDelete}
            color="error"
            variant="contained"
            autoFocus
          >
            Confirm
          </LoadingButton>
        </DialogActions>
      </Dialog>
    </Grid>
  );
};

export default ViewContentLibFolder;
